import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import clsx from "clsx";

import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";

import AppBar from "./components/ButtonAppBar.js";
//import Drawer from "./components/Drawer.js";
import Drawer from "@material-ui/core/Drawer";
import EmojiPeopleIcon from "@material-ui/icons/EmojiPeople";
import AddAlertIcon from "@material-ui/icons/AddAlert";

import Card from "@material-ui/core/Card";
import Stepper from "./components/Stepper.js";

const useStyles = makeStyles({
  list: {
    width: 300,
  },
  fullList: {
    width: "auto",
  },
  marginTop: {
    marginTop: 65,
  },
});

const Routes = () => {
  const classes = useStyles();
  const [state, setState] = React.useState(false);

  const toggleDrawer = () => {
    setState(!state);
  };

  const list = () => (
    <div className={clsx(classes.list, classes.marginTop)} role="presentation">
      <Divider />
      <List>
        <ListItem button>
          <ListItemIcon>
            <EmojiPeopleIcon />
          </ListItemIcon>
          <ListItemText primary="Disponibilidad" />
        </ListItem>
        <ListItem button>
          <ListItemIcon>
            <AddAlertIcon />
          </ListItemIcon>
          <ListItemText primary="Panel de Control" />
        </ListItem>
      </List>
      <Divider />
    </div>
  );

  return (
    <React.Fragment>
      <AppBar openDrawer={toggleDrawer} state={state} />

      <Drawer anchor="left" open={state} onClose={toggleDrawer}>
        {list()}
      </Drawer>

      <Switch>
        <Redirect from="/" to="/calendario" exact />
        <Route path="/calendario">
          <div
            style={{
              margin: "1.2rem auto",
              maxWidth: "80vw",
            }}
          >
            <Paper
              style={{ margin: "0 auto", maxWidth: "60vw" }}
              square
              elevation={0}
            >
              <Typography style={{ padding: "0.5rem 2rem" }}>
                {/*  Scheduling a 1 hour appointment on Saturday, June 27th at 9:00 
                Scheduling your appointment on time!*/}
                ¬°Programe su pr√≥xima cita seg√∫n su tiempo y disponibilidad del
                servicio!
              </Typography>
            </Paper>
            <Card
              style={{
                padding: "10px 10px 25px 10px",
                maxWidth: "60vw",
                margin: "1.2rem auto",
              }}
            >
              <Stepper />
            </Card>
          </div>
        </Route>
        <Route path="/test">
          <h1>TEST</h1>
        </Route>
      </Switch>
    </React.Fragment>
  );
};

export default Routes;
