import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";

import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import DateFnsUtils from "@date-io/date-fns";

import StepButton from "@material-ui/core/StepButton";

import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";

import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";

import TextField from "@material-ui/core/TextField";

import SendIcon from "@material-ui/icons/Send";

import {
  KeyboardDatePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
  textFields: {
    "& > *": {
      margin: theme.spacing(1),
      width: "20rem",
    },
  },
}));

export default function VerticalLinearStepper() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = [
    "Seleccione un d√≠a en el calendario",
    "Seleccione el turno y la hora de su conveniencia",
    "Suministre su informaci√≥n de contacto",
  ];

  const [turno, setTurno] = React.useState(1);
  const handleTurno = (event) => {
    setTurno(event.target.value);
  };
  const [horario, setHorario] = React.useState("1");
  const handleHorario = (event) => {
    setHorario(event.target.value);
    handleNext();
  };

  const [selectedDate, setSelectedDate] = React.useState(new Date());

  const handleDateChange = (date) => {
    setSelectedDate(date);
    handleNext();
  };

  const [text, setText] = React.useState({
    nombre: null,
    apellido: null,
    email: null,
    telf: null,
  });

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  /*   const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  }; */

  /*   const handleReset = () => {
    setActiveStep(0);
  }; */

  return (
    <div className={classes.root}>
      <Stepper activeStep={activeStep} orientation="vertical">
        <Step key={`label1`}>
          <StepButton
            onClick={() => {
              setActiveStep(0);
            }}
          >{`${steps[0]}`}</StepButton>
          <StepContent>
            <div className={classes.actionsContainer}>
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                  margin="normal"
                  id="date-picker-dialog"
                  label="Calendario desplegable"
                  format="MM/dd/yyyy"
                  value={selectedDate}
                  onChange={handleDateChange}
                  KeyboardButtonProps={{
                    "aria-label": "change date",
                  }}
                />
              </MuiPickersUtilsProvider>
            </div>
          </StepContent>
        </Step>
        <Step key={`label2`}>
          <StepButton
            onClick={() => {
              setActiveStep(1);
            }}
          >{`${steps[1]}`}</StepButton>
          <StepContent>
            <div className={classes.actionsContainer}>
              <FormControl
                style={{ width: "7rem" }}
                className={classes.formControl}
              >
                <InputLabel id="select-label">AM / PM</InputLabel>
                <Select
                  labelId="select-label"
                  id="demo-simple-select"
                  value={turno}
                  onChange={handleTurno}
                >
                  <MenuItem value={1}>AM</MenuItem>
                  <MenuItem value={2}>PM</MenuItem>
                </Select>
              </FormControl>
              <FormControl style={{ display: "block" }} component="fieldset">
                <RadioGroup
                  aria-label="gender"
                  name="gender1"
                  value={horario}
                  onChange={handleHorario}
                >
                  {turno === 1 ? (
                    <>
                      <FormControlLabel
                        value="1"
                        control={<Radio style={{ color: "#3f51b5" }} />}
                        label="9:00am - 10:00am"
                      />
                      <FormControlLabel
                        value="2"
                        control={<Radio style={{ color: "#3f51b5" }} />}
                        label="10:00am - 11:00am"
                      />
                      <FormControlLabel
                        value="3"
                        control={<Radio style={{ color: "#3f51b5" }} />}
                        label="11:00am - 12:00am"
                      />
                    </>
                  ) : (
                    <>
                      <FormControlLabel
                        value="4"
                        control={<Radio style={{ color: "#3f51b5" }} />}
                        label="2:00pm - 3:00pm"
                      />
                      <FormControlLabel
                        value="5"
                        control={<Radio style={{ color: "#3f51b5" }} />}
                        label="3:00pm - 4:00pm"
                      />
                      <FormControlLabel
                        value="6"
                        control={<Radio style={{ color: "#3f51b5" }} />}
                        label="5:00pm - 6:00pm"
                      />
                    </>
                  )}
                </RadioGroup>
              </FormControl>
            </div>
          </StepContent>
        </Step>
        <Step key={`label3`}>
          <StepButton
            onClick={() => {
              setActiveStep(2);
            }}
          >{`${steps[2]}`}</StepButton>
          <StepContent>
            <form
              style={{ display: "flex", flexDirection: "column" }}
              className={classes.textFields}
              noValidate
              autoComplete="off"
            >
              <TextField
                placeholder="Nombre"
                id="standard-basic"
                label="Nombre"
                onChange={(event) => {
                  const { value } = event.target;
                  setText({ ...text, nombre: value });
                }}
              />
              <TextField
                placeholder="Apellido"
                id="standard-basic"
                label="Apellido"
                onChange={(event) => {
                  const { value } = event.target;
                  setText({ ...text, apellido: value });
                }}
              />
              <TextField
                placeholder="Email"
                id="standard-basic"
                label="Email"
                onChange={(event) => {
                  const { value } = event.target;
                  setText({ ...text, email: value });
                }}
              />
              <TextField
                placeholder="Tel√©fono"
                id="standard-basic"
                label="Tel√©fono"
                onChange={(event) => {
                  const { value } = event.target;
                  setText({ ...text, telf: value });
                }}
              />
            </form>
            {(!text.nombre || !text.apellido || !text.email || !text.telf) ===
            false ? (
              <Button
                style={{
                  width: "100%",
                  marginTop: "2rem",
                  backgroundColor: "#3f51b5",
                }}
                variant="contained"
                color="secondary"
                className={classes.button}
                endIcon={<SendIcon>send</SendIcon>}
              >
                Agendar Consulta
              </Button>
            ) : (
              <Button
                style={{
                  width: "100%",
                  marginTop: "2rem",
                }}
                variant="contained"
                disabled
                color="secondary"
                className={classes.button}
                endIcon={<SendIcon>send</SendIcon>}
              >
                Complete toda la informaci√≥n para continuar
              </Button>
            )}
          </StepContent>
        </Step>
      </Stepper>
    </div>
  );
}
